import { useState } from "react";
import Modal from "react-bootstrap/Modal";
import OwlCarousel from "react-owl-carousel";
import "owl.carousel/dist/assets/owl.carousel.css";
import "owl.carousel/dist/assets/owl.theme.default.css";
import VisibilitySensor from "react-visibility-sensor";

const About = () => {
  const [showReadMore, setShowReadMore] = useState(false);

  function pronounce() {
    const audioEl = document.getElementsByClassName("audio-element")[0];
    audioEl.play();
  }

  let ReadMore = () => {
    return (
      <Modal
        show={showReadMore}
        onHide={() => {
          setShowReadMore(false);
        }}
        backdrop="true"
        keyboard={false}
        scrollable={true}
        contentClassName="modal-container"
        size="lg"
      >
        <Modal.Body className="modal-body">
          <div className="cert-container">
            <button
              className="close-btn"
              onClick={() => {
                setShowReadMore(false);
              }}
            >
              <i className="fa fa-times"></i>
            </button>

            <audio className="audio-element">
              <source src="/assets/myname.mp3"></source>
            </audio>
            <br />
            <h4 className="title">About me</h4>
            <p>
              My interest in programming started when I discovered CMD, yes
              Windows command prompt. I wanted to hack a wifi password and I saw
              that i can use the command line for that (does not work) but turns
              out you can take a look at saved passwords that blew my mind. So I
              googled <b>“all cmd commands”</b> and started copying and pasting
              each one to see what happens. I went through all of them and the
              next day when i open my laptop my windows crashed and it wouldn't
              open. So that made me realize to <b>NEVER</b> just copy and paste
              code into a command line. I took it to a repair shop and got it
              fixed and wanted to learn more.
              <br />
              <br />
              <h4 className="title">My first programming language</h4>
              The first thing I learned was how to run CMD commands with an
              executable file with batch programming. I started making CMD based
              applications a file hider being one of them.
              <br />
              <br />
              After playing around with it for a while i had some ideas like a
              desktop cleanup script but i felt restricted and wanted to get
              into more advanced software development so i googled{" "}
              <b>“best programming languages for beginners”</b> and after a few
              youtube videos i went with python. <br />
              <br />
              And it wasn't as easy as batch programming easier actually, super
              simple to write and execute no need for compilers or anything, so
              I started taking a course on Youtube. I went through the course
              within the month, I learned about <b>Syntax</b>, <b>Comments</b>,{" "}
              <b>Data types</b>,<b>variables</b>, <b>Booleans</b>,{" "}
              <b>Operators</b> <b>,lists</b> <b>, if/else statements</b>
              <b>,Loops</b> and <b>dictionaries</b>. I went on to learn about
              some python modules and started working on more advanced projects.
              <br />
              My first useful project came after months after using python which
              was a downloads cleanup script which movies organizes files into
              specific folder{" "}
              <b>
                for example: image.jpg would be moved to the jpg folder and .mp4
                files to videos folder{" "}
              </b>
              by then i've learned about modules, functions, and classes which
              allowed me to make more advanced web applications.
              <br />
              <br />
              <h4 className="title">Web Development</h4>I had experience with
              html and css from editing templates for fun but i didn't really
              know what i was doing at all actually, so i decided to learn Html
              and CSS, I discovered w3schools.com and got to work, after a few
              weeks i was pretty comfortable with both languages, and after a
              few months i decided to make a portfolio page for my self which
              lead me to learning a lot of key concepts. i wanted my portfolio
              visitors to send me emails so i got into PHP (didn't learn
              Javascript at this point) so i got the basics of PHP all my python
              skills applied to this as well so it was pretty easy to
              understand, With the help of google i was making web pages that
              can submit forms directly to emails with HTML, CSS, Bootstrap and
              PHP (No Javascript) I felt like i knew it all{" "}
              <img
                src="https://alohe.github.io/emojicloud/svg/Rolling on the floor laughing.svg"
                width="30px"
              />
              and wanted to make money with my skills that took me years to
              craft (2 years) and started freelancing.
              <br />
              <br />
              Lol yea i know crazy right i was 16 at the time.
              <br /> i signed up to upwork and stated applying, To my surprise i
              had a lot of responses on my proposals, after 2 weeks or so 1
              client decided to give me the job, after a month working on the
              site which was good enough, since i was good at css it didn't look
              bad at all, after a few weeks i got my first paycheck at 17 which
              was $100, and it felt like i didn't earn it because it was fun
              making the site i almost felt guilty that i got paid for it.
              <br />
              <br />
              After that i had no luck with winning projects on upwork, so i
              asked a friend who worked at a web design agency to get me a job
              as a front-end developer and he asked his boss and got me a job
              based on the fact that i had a remote work experience with my fist
              client on upwork, so i spent the next 2 and a half years working
              along senior developers, learning Javascript, nodejs, mysql,
              firebase and a lot more technologies including flutter, after the
              2+ years working there i was officially a full stack developer
              working on full stack web applications with Html, CSS, Javascript,
              Jquery, Node.js, Express.js, Mysql and Firebase.
              <br />
              <br />
              <h4 className="title">Now</h4>
              Fast forward 2 years later, I've branched out on my own and have
              worked on various freelance projects and planning on adding
              Next.js and svelte to my skill set soon.
              <br />
              <br />
              Between those 4+ years i’ve dabbled with app development with
              Flutter. I made a few apps with it and decided web development was
              where my heart was so I’m currently working as a freelance
              developer and occasionally working on passion projects and
              launching them on product hunt.
            </p>
            <div className="row mt-5 mb-5">
              <iframe
                className="col-lg-6 rounded"
                style={{ border: "none" }}
                src="https://cards.producthunt.com/cards/posts/304360?v=1"
                width="500"
                height="405"
                frameborder="0"
                scrolling="no"
                allowfullscreen
              ></iframe>

              <iframe
                className="col-lg-6 rounded"
                style={{ border: "none" }}
                src="https://cards.producthunt.com/cards/posts/287988?v=1"
                width="500"
                height="405"
                frameborder="0"
                scrolling="no"
                allowfullscreen
              ></iframe>
            </div>
            <div className="about-hi hor m-0">
              <a
                href="https://t.me/alemalohe"
                className=""
                target="_blank"
                rel="noopener noreferrer"
              >
                <button>
                  <i className="fab fa-telegram "></i>
                </button>
              </a>

              <a
                href="https://twitter.com/alemalohe"
                className=""
                target="_blank"
                rel="noopener noreferrer"
              >
                <button>
                  <i className="fab fa-twitter "></i>
                </button>
              </a>

              <a
                href="https://www.linkedin.com/in/alemalohe/"
                className=""
                target="_blank"
                rel="noopener noreferrer"
              >
                <button>
                  <i className="fab fa-linkedin "></i>{" "}
                </button>
              </a>
            </div>
            <h3 className="title" hidden>
              Certificates
            </h3>
            <img
              src="https://www.sololearn.com/certificates/course/en/9278806/1097/landscape/png"
              className="img-fluid cert"
              hidden
            />
            <img
              src="https://www.sololearn.com/certificates/course/en/9278806/1024/landscape/png"
              className="img-fluid cert"
              hidden
            />
            <img
              src="https://www.sololearn.com/certificates/course/en/9278806/1023/landscape/png"
              className="img-fluid cert"
              hidden
            />
            <img
              src="https://www.sololearn.com/certificates/course/en/9278806/1073/landscape/png"
              className="img-fluid cert"
              hidden
            />
            <img
              src="https://www.sololearn.com/certificates/course/en/9278806/1014/landscape/png"
              className="img-fluid cert"
              hidden
            />
          </div>
        </Modal.Body>
      </Modal>
    );
  };

  return (
    <div className="about">
      {showReadMore ? <ReadMore /> : ""}

      <div className="container">
        <div className="row skills">
          <div className="col-lg-12 text-center">
            <VisibilitySensor>
              {({ isVisible }) => {
                return (
                  <div
                    className={
                      isVisible ? "face p-3 hi-container" : "p-3 hi-container"
                    }
                  >
                    <img
                      src="assets/img/face.png"
                      width="180px"
                      style={{ borderRadius: "100%" }}
                      alt="https://alohe.github.io"
                    />
                    <div>
                      <img
                        hidden={isVisible ? false : true}
                        className="hi"
                        src="https://alohe.github.io/emojicloud/svg/Hand%20with%20fingers%20splayed.svg"
                      />
                    </div>
                  </div>
                );
              }}
            </VisibilitySensor>

            <h1 className="h1">About me</h1>
          </div>
          <div className="col-lg-4">
            <p className="h2">
              From custom design's to fully functional web applications, I can
              help you turn your personal or business concepts into exceptional
              website's that are robust, cost-effective, secure, scalable, and
              easily manageable.
              <br />
              <u className="text-dark">
                <a
                  data-toggle="collapse"
                  onClick={() => {
                    setShowReadMore(true);
                  }}
                  role="button"
                  aria-expanded="false"
                  aria-controls="More"
                  className="pointer"
                >
                  <button className="more-btn">My Story</button>
                </a>
              </u>
            </p>
          </div>
          <div className="col-lg-8 skill-card-container">
            <p className="">
              Here are a few technologies I've been working with recently
            </p>
            <div className="skill-cards">
              <div className="skill-card">
                <button className="icon mr-1 ml-3">
                  <i className="fab fa-react"></i>
                </button>
                <h3 className="language">React</h3>
              </div>
              <div className="skill-card">
                <button className="icon mr-1 ml-3">
                  <i className="fab fa-js"></i>
                </button>
                <h3 className="language">JavaScript (ES6+)</h3>
              </div>
              <div className="skill-card">
                <button className="icon mr-1 ml-3">
                  <i className="fab fa-node-js"></i>
                </button>
                <h3 className="language">Node.js</h3>
              </div>
              <div className="skill-card">
                <button className="icon mr-1 ml-3">
                  <i className="fab fa-bootstrap"></i>
                </button>
                <h3 className="language">Bootstrap</h3>
              </div>
              <div className="skill-card" hidden>
                <button className="icon mr-1 ml-3">
                  <img
                    src="https://upload.wikimedia.org/wikipedia/commons/thumb/2/29/Postgresql_elephant.svg/1200px-Postgresql_elephant.svg.png"
                    alt=""
                  />
                </button>
                <h3 className="language">PostgreSQL</h3>
              </div>
              <div className="skill-card">
                <button className="icon mr-1 ml-3">
                  <img
                    src="https://sass-lang.com/assets/img/logos/logo-b6e1ef6e.svg"
                    alt=""
                  />
                </button>
                <h3 className="language">Sass</h3>
              </div>
              <div className="skill-card">
                <button className="icon mr-1 ml-3">
                  <img
                    src="https://www.kepler-dev.com/wp-content/uploads/2016/09/mysqllogo.png"
                    alt=""
                  />
                </button>
                <h3 className="language">Mysql</h3>
              </div>
              <div className="skill-card">
                <button className="icon mr-1 ml-3">
                  <img
                    src="https://cdn4.iconfinder.com/data/icons/google-i-o-2016/512/google_firebase-2-512.png"
                    alt=""
                  />
                </button>
                <h3 className="language">Firebase</h3>
              </div>
              <div className="skill-card">
                <button className="icon mr-1 ml-3">
                  <img
                    src="https://1000logos.net/wp-content/uploads/2020/06/Illustrator-Logo.png"
                    alt=""
                  />
                </button>
                <h3 className="language">Illustrator</h3>
              </div>
              <div className="skill-card">
                <button className="icon mr-1 ml-3">
                  <img
                    src="https://e1.pngegg.com/pngimages/188/872/png-clipart-macos-app-icons-framer-thumbnail.png"
                    alt=""
                  />
                </button>
                <h3 className="language">Framer</h3>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export { About };
